@import url(https://db.onlinewebfonts.com/c/f4d1593471d222ddebd973210265762a?family=Pokemon);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(132, 196, 238);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Pokemon";
  src: url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.eot");
  src: url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.eot?#iefix")
      format("embedded-opentype"),
    url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.woff2")
      format("woff2"),
    url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.woff")
      format("woff"),
    url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.ttf")
      format("truetype"),
    url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.svg#Pokemon")
      format("svg");
}
.App {
  font-family: Pokemon;
  text-align: center;
  letter-spacing: 2px;
  background-color: rgb(132, 196, 238);
  min-height: 100vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

ul {
  list-style: none;
}
h1 {
  color: yellow;
  text-shadow: 5px 5px 5px blue;
  background-color: tomato;
  margin: 0;
}
/* * {
  background-color: rgb(132, 196, 238);
} */
.search-input {
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  border: none;
  background-color: wheat;
  border-radius: 10px;
  height: 30px;
  width: 30%;
  margin: auto;
}
.search-button {
  width: 4%;
  padding: 0% 1%;
  cursor: pointer;
}

.catch-release-btn {
  width: 5%;
  padding: 2%;
  cursor: pointer;
}
.catch-release-btn:hover {
  margin: 0.3%;
}

.pokemon-collection-item,
button,
span {
  cursor: pointer;
}

.spinner-div {
  display: none;
}
.loader {
  z-index: 1;
  -webkit-animation: loader infinite 5s linear;
          animation: loader infinite 5s linear;
  position: absolute;
  -webkit-align-content: center;
          align-content: center;
  display: -webkit-flex;
  display: flex;
  left: 20%;
  bottom: 22%;
  width: 50%;
  height: 50%;
}
.spinner-img {
  width: 100%;
  height: 100%;
}
.blur {
  -webkit-filter: blur(8px);
          filter: blur(8px);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.collection-div {
  display: inline-block;
  max-width: 20%;
}

.ul-pokemon-types {
  display: grid;
  grid-template-columns: 20% 20% 20% 20% 20%;
  grid-template-rows: -webkit-max-content;
  grid-template-rows: max-content;
  grid-column-gap: 1.5%;
  -webkit-column-gap: 1.5%;
          column-gap: 1.5%;
  grid-row-gap: 1%;
  row-gap: 1%;
  position: absolute;
  padding-right: 13%;
  /* display: inline-block; */
  max-width: -webkit-min-content;
  max-width: min-content;
}

.li-pokemon-details {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.div-pokemon-details {
  text-align: start;
}
.div-pokemon-types {
  border: red solid 0.1em;
}
.pokemon-list-by-type {
  display: block;
}
.next-btn,
.get-collection-btn {
  font-family: pokemon;
  letter-spacing: 2px;
  color: yellow;
  text-shadow: 5px 5px 5px blue;
  background-color: tomato;
}
@-webkit-keyframes loader {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes loader {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

